import "./App.css";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import SignIn from "./pages/login/SignIn";
import Home from "./pages/home/Home";
import Historico from "./pages/historico/Historico";
import NovaLeitura from "./pages/nova_leitura/NovaLeitura";
import { MuiThemeProvider, createMuiTheme } from "@material-ui/core/styles";
import ResultadoNovaLeitura from "./pages/resultado_nova_leitura/ResultadoNovaLeitura";

const theme = createMuiTheme({
  palette: {
    primary: {
      main: "#00bcd4",
    },
    secondary: {
      main: "#fff",
    },
  },
});

function App() {
  return (
    <MuiThemeProvider theme={theme}>
      <Router>
        <div>
          {/* A <Switch> looks through its children <Route>s and
            renders the first one that matches the current URL. */}
          <Switch>
            <Route exact path={process.env.PUBLIC_URL + "/"}>
              <SignIn />
            </Route>
            <Route path={process.env.PUBLIC_URL + "/home"}>
              <Home />
            </Route>
            <Route path={process.env.PUBLIC_URL + "/historico"}>
              <Historico />
            </Route>
            <Route path={process.env.PUBLIC_URL + "/nova_leitura"}>
              <NovaLeitura />
            </Route>
            <Route path={process.env.PUBLIC_URL + "/resultado_nova_leitura"}>
              <ResultadoNovaLeitura />
            </Route>
          </Switch>
        </div>
      </Router>
    </MuiThemeProvider>
  );
}

export default App;
